body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#main-container {
  height: 100%; }

#inner-container {
  width: 100%;
  height: 100%;
  min-width: 600px;
  min-height: 400px; }

#sidebar {
  display: none;
  max-width: 440px;
  background: #141414;
  height: 100%;
  position: fixed;
  overflow: auto; }
  #sidebar::-webkit-scrollbar {
    width: 5px;
    height: 8px;
    background-color: #fffaf1;
    /* or add it to the track */ }
  #sidebar::-webkit-scrollbar-thumb {
    background: #141414; }
  #sidebar .sidebar-top {
    height: 40%;
    display: block;
    margin-left: auto;
    margin-right: auto;
    min-height: 300px; }
  #sidebar .sidebar-bottom {
    height: 40%;
    display: block; }
  @media (min-width: 992px) {
    #sidebar {
      width: 360px;
      display: block; } }

#sidebar-padding {
  display: inline-block;
  height: 100%; }
  @media (min-width: 992px) {
    #sidebar-padding {
      width: 360px; } }

#content-wrap {
  width: 60%;
  height: 100%;
  display: inline-block;
  padding: 10px 10px 10px 0px;
  margin-left: auto;
  margin-right: auto; }

.content-inner {
  overflow-y: auto;
  height: 100%;
  padding: 20px;
  padding-left: 50px; }

body {
  background: #fffaf1;
  min-width: 850px;
  min-height: 550px; }

a {
  text-decoration: none;
  text-underline: none; }

.cool-card {
  width: 320px;
  height: 180px;
  background: grey;
  border-radius: 5px;
  background-size: cover;
  background-position: center center;
  box-shadow: 0px 10px 20px -5px rgba(0, 0, 0, 0.3);
  -webkit-transition: box-shadow 0.5s;
  transition: box-shadow 0.5s;
  will-change: transform;
  border: 15px solid white;
  margin-bottom: 20px; }

.cool-card:hover {
  box-shadow: 0px 20px 20px -10px rgba(0, 0, 0, 0.4); }

.modal-button:hover {
  cursor: pointer; }

#creative-page:empty {
  position: absolute;
  top: calc(50% - 4em);
  left: calc(50% - 4em);
  width: 6em;
  height: 6em;
  border: 1.1em solid rgba(0, 0, 0, 0.2);
  border-left: 1.1em solid #000000;
  border-radius: 50%;
  -webkit-animation: load8 1.1s infinite linear;
          animation: load8 1.1s infinite linear; }

@-webkit-keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

ul {
  list-style: none;
  -webkit-margin-before: 0 !important;
          margin-block-start: 0 !important;
  -webkit-margin-after: 0 !important;
          margin-block-end: 0 !important;
  -webkit-padding-start: 0px !important;
          padding-inline-start: 0px !important; }
  ul li {
    margin-bottom: 1.5em;
    padding-right: 0px; }

.nav {
  display: block;
  font-size: 20px;
  text-align: center; }
  .nav a {
    color: #fff; }


.creative-card {
  margin-bottom: 100px; }
  .creative-card .desc-part {
    display: inline-block;
    width: 10%;
    height: 180px;
    margin-left: 30px; }
  .creative-card .img-part {
    display: inline-block;
    float: left; }

.creative-image-full, .creative-image-full-preload {
  max-width: 80vh;
  max-height: 80vh;
  width: auto;
  height: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  border: 30px solid #000;
  padding: 16px 32px 24px;
  box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, 0.2), 0px 5px 8px 0px rgba(0, 0, 0, 0.14), 0px 1px 14px 0px rgba(0, 0, 0, 0.12);
  background-color: #fff; }

.creative-image-full-container {
  width: 80%;
  height: auto;
  max-height: 80vh;
  margin-left: auto;
  margin-right: auto; }

.creative-image-full-preload img {
  filter: blur(8px);
  -webkit-filter: blur(8px); }

#avatar-container {
  height: 100%;
  display: flex;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;
  width: auto;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0; }
  #avatar-container svg {
    -webkit-transform: scale(1) !important;
            transform: scale(1) !important;
    opacity: 1;
    width: 100%;
    height: 100%;
    margin-top: -25px;
    position: relative;
    z-index: 9;
    fill: #141414; }
  #avatar-container .svg-container {
    width: inherit;
    height: inherit;
    justify-content: center;
    margin-left: auto;
    margin-right: auto; }

.shape {
  height: 230px;
  width: 230px;
  overflow: hidden;
  position: absolute;
  z-index: 5;
  -webkit-transition: all .51s;
  transition: all .51s;
  border-radius: 50%; }
  .shape.hovered {
    border-radius: 60% 40% 56% 44% / 35% 33% 67% 65%; }

.avatar {
  width: 100%;
  height: 100%;
  position: absolute;
  background-size: 165%;
  align-items: center;
  background-position-x: 69%;
  background-position-y: 35%;
  opacity: 1;
  z-index: 2;
  -webkit-transform: rotate(7deg) scaleX(-1);
          transform: rotate(7deg) scaleX(-1); }

@-webkit-keyframes morph {
  0% {
    border-radius: 60% 40% 56% 44% / 35% 33% 67% 65%; }
  50% {
    border-radius: 67% 33% 27% 73% / 50% 37% 63% 50%; }
  100% {
    border-radius: 60% 40% 56% 44% / 35% 33% 67% 65%; } }

@keyframes morph {
  0% {
    border-radius: 60% 40% 56% 44% / 35% 33% 67% 65%; }
  50% {
    border-radius: 67% 33% 27% 73% / 50% 37% 63% 50%; }
  100% {
    border-radius: 60% 40% 56% 44% / 35% 33% 67% 65%; } }

@-webkit-keyframes spin {
  to {
    -webkit-transform: rotate(1turn);
            transform: rotate(1turn); } }

@keyframes spin {
  to {
    -webkit-transform: rotate(1turn);
            transform: rotate(1turn); } }

g .avatar {
  height: 300px;
  width: 300px; }

.mgrid {
  overflow: hidden;
  display: flex;
  justify-content: center;
  background: #f0f0f0;
  padding: 8px;
  height: 100vh; }

.mgrid-list {
  position: relative;
  font-family: -apple-system, BlinkMacSystemFont, avenir next, avenir, helvetica neue, helvetica, ubuntu, roboto, noto, segoe ui, arial, sans-serif;
  width: 100%;
  height: 100%; }

.mgrid-list > div {
  position: absolute;
  will-change: transform, width, height, opacity;
  padding: 8px;
  box-sizing: border-box; }

.mgrid-list > div > div {
  position: relative;
  background-size: cover;
  background-position: center center;
  width: 100%;
  height: 100%;
  overflow: hidden;
  text-transform: uppercase;
  font-size: 10px;
  line-height: 10px;
  border-radius: 4px;
  box-shadow: 0px 10px 50px -10px rgba(0, 0, 0, 0.2); }

